@import "../variables/_variables.sass";

.main-index {
  .navbar {
    background-color: #fff;
    transition: all .25s;
    border: none;
    -webkit-box-shadow: 0px 10px 35px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 35px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 35px -20px rgba(0,0,0,0.5);
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    .holder {
      height: 70px;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -ms-flex-align: center;
      -webkit-align-items: center;
      -webkit-box-align: center;
      align-items: center;
      transition: height .25s;
      .content-holder {
        width: 100%; } }
    .left-container {
      background-color: rgb(228, 161, 94); }
    .center-container {
      background-color: rgb(195, 160, 228);
      text-align: center; }
    .right-container {
      background-color: rgb(219, 128, 139);
      text-align: right; } } }


@media (max-width: 767px) {
  .main-index {
    .navbar {
      .content {
        height: 70px;
        padding: 0px !important;
        .logo-container {
          .logo {
            font-size: 23px;
            .logo-initials {
              font-size: 25px; } } }
        .contact-link-container {
          .contact-link {
            font-size: 16px;
            padding: 5px 20px; } } } } }
  padding: 0px !important; }
